import React, { Component } from 'react';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';

import Input from '@material-ui/core/Input';
import InputAdornment from '@material-ui/core/InputAdornment';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import SearchIcon from '@material-ui/icons/Search';
import { url } from '../constant';

// import Card from '@material-ui/core/Card';

const styles = theme => ({
    search: {
        textAlign: 'center',
        margin: 'auto',
        top: '0px',
        left: '0px',
        right: '0px',
        padding: '10px'
    },

    card: {
        margin: '10px'
    },

    imageDiv: {
        display: 'inline-block',
        marginRight: '20px'
    },

    textDiv: {
        display: 'inline-block',
        position: 'absolute',
        marginTop:'10px'
    },

    image: {
        maxWidth: '70px',
        maxHeight: '70px',
        borderRadius: '50%'
    },

    listItem: {
        margin: '10px',
        padding: '10px',
        borderBottom: '1px solid #eee'
    }
})

class SearchTab extends Component {

    state = {
        isLoading: true,
        data: []
    };

    componentWillMount() {
        if(navigator.geolocation) {
            try {
                navigator.geolocation.getCurrentPosition((position) => {
                    let latitude = position.coords.latitude;
                    let longitude = position.coords.longitude;
                    fetch(url + `officeNearLocation?location=${latitude},${longitude}&distance=30000`)
                    .then(res => res.json())
                    .then(res => {                
                        if(res.status == 'Success') {
                            this.setState({
                                isLoading: false,
                                data: res.data
                            })
                        }
                    })
                });
            } catch(e) {
                alert(e);
            }
        } else {
            alert("ok")
        }
    }
    
    render() {
        let { classes } = this.props;
        

        return (
            <div>
                {
                    this.state.isLoading &&
                    (
                        <div style={{width: '100vw', height: '30vh', display: 'flex'}}>
                            <CircularProgress style={{ margin: 'auto' }} color="secondary" />
                        </div>
                    )
                }
                {
                    (!this.state.isLoading) && 
                    this.state.data.map((data, inded) => {
                        return (
                            <div className={classes.listItem}>
                                <div className={classes.imageDiv}>
                                    <img src={"https://picsum.photos/300?random&dd=" + Math.random() } className={classes.image} alt={data.name} />
                                </div>
                                <div className={classes.textDiv}>
                                    <Typography variant="subheading">{data.Name}</Typography>
                                    <Typography variant="subheading">Contact - {data.contactNo}</Typography>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        );
    }
}

SearchTab.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SearchTab);