import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import DashboardImage from '../res/1.jpg';
import AmbulanceImage from '../res/2.jpg';
import NgoImage from '../res/3.jpg';
import ProfileImage from '../res/4.jpg';
import { Divider } from '@material-ui/core';

const styles = {
  appBar: {
    position: 'relative',
  },
  flex: {
    flex: 1,
  },
  body: {
    textAlign: 'center',
    margin: '10px',
  },
  image: {
    width: '200px',
    display: 'block',
    margin: '10px auto',
  }
};

let Transition = (props) => (<Slide direction="up" {...props} />)

let HelpPara = (props) => (
  <div style={{ margin: "10px auto" }}>
    <br />
    <Typography variant="title">{props.title}</Typography>
    <img className={props.classes.image} src={props.image} />
    <Typography variant="body2">{props.text}</Typography>
  </div>
)

let DialogBody = (props) => (
  <div className={props.classes.body}>
    <HelpPara {...props} title="Dashboard" text={"This is app Dashboard. All emergency services will appear here."} image={DashboardImage} />
    <HelpPara {...props} title="Service" text={"Tapping on any of service appearing on dashboard will open a dialog asking for some description. Fill appropriate discription and tap send button. This will send your location and account details to the service"} image={AmbulanceImage} />
    <HelpPara {...props} title="Search" text={"You can search nearby NGOs and other services in search tab."} image={NgoImage} />
    <HelpPara {...props} title="Profile" text={"You can update your profile details in profile tab. You can also logout from here."} image={ProfileImage} />
  </div>
)

let FullScreenDialog = (props) => {
  const { classes } = props;
  return (
      <Dialog
          style = {{ background: '#eee' }}
          fullScreen
          open={props.open}
          onClose={props.handleClose}
          TransitionComponent={Transition}
      >
          <AppBar className={classes.appBar}>
              <Toolbar>
                  <IconButton color="inherit" onClick={props.handleClose} aria-label="Close">
                  <CloseIcon />
                  </IconButton>
                  <Typography variant="h6" color="inherit" className={classes.flex}>
                  Help
                  </Typography>
              </Toolbar>
          </AppBar>
          <DialogBody {...props} />
      </Dialog>
  );
}

FullScreenDialog.propTypes = {
  classes: PropTypes.object.isRequired,
  open: PropTypes.bool.isRequired,
  handleClose: PropTypes.func.isRequired
};

export default withStyles(styles)(FullScreenDialog);